@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  background-color: #F3F8FE;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
ul{
  list-style: none;
  padding: 0;
}
a{
  text-decoration: none;
}
button{
  border: none;
}



.App {
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header{
    padding:10px 5%;
    background-color: #fff;
    box-shadow: 0 0 10px 0 rgba(0,0,0,.3);
}
nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.logo{
    width: 200px;
}
.userProfile{
    display: flex;
    align-items: center;
}
.userProfile p {
    margin-right: 10px;
    width: 46px;
    background-color: #0077FF;
    padding: 11px 0;
    border-radius: 100%;
    color: #fff;
    font-weight: 600;
    text-align: center;
}
.profile{
    position: relative;
    cursor: pointer;
    color: #666;
    font-weight: 500;
}
.dropdownIcon{
    width: 16px;
    margin-left: 10px;
}
ul.profileDropdown{
    position: absolute;
    left: 0;
    width: 100%;
    text-align: center;
    background-color: #f2f2f2;
    top: 100%;
    display: none;
}
ul.profileDropdown li{
    padding: 10px 5px;
    border-bottom: 1px solid #ccc;
    cursor: pointer;
}
.profile:hover .profileDropdown{
    display: block;
}
.mobileShow{
    display: none;
}
@media only screen and (max-width:767px){
    nav{
        display: none;
    }
    .mobileShow{
        display: block;
    }
}
.formCont{
  padding:30px 22px;
  overflow-x: auto;
}
.mainTab{
  width: 1024px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.mainTab li p:first-child{
  width: 40px;
  background-color: #EEF1F7;
  padding: 9px 0;
  border-radius: 100%;
  color: #ccc;
  font-weight: 600;
  text-align: center;
  margin: auto;
}
.activeTab p:first-child{
  background-color: #0077FF !important;
  color: #fff !important;
}
.activeTab p:nth-child(2){
  color: #0077FF !important;
  font-weight: bold;
}
.mainTab::after{
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  width: 85%;
  height: 1px;
  border-bottom: 5px dotted #EEF1F7;
  top: 17px;
  z-index: -1;
}
.formContainer{
  width: 80%;
  margin: auto;
  box-shadow: 0 0 10px 0 rgba(0,0,0,.15);
  padding:50px 100px;
  background-color: #fff;
  box-sizing: border-box;
}
.formSec{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.formSec{
  display: none;
}
h2{
  width: 100%;
}
.inputField{
  width: 30%;
  margin-bottom: 30px;
}
.inputField:nth-child(5),.inputField:nth-child(6){
  width: 100%;
}
.info{
  width: 16px;
  vertical-align: middle;
}
label{
  display: block;
  margin-bottom: 10px;
}
input,select,textarea{
  width: 100%;
  height: 40px !important;
  display: block;
  padding: 0;
  border: none;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding-left: 5px;
  color: #666;
}
select{
  height: 42px !important;
  color: #ccc;
}
textarea,#fileAttachment{
  height: 80px !important;
}
::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #ccc;
}
::-moz-placeholder { /* Firefox 19+ */
  color: #ccc;
}
:-ms-input-placeholder { /* IE 10+ */
  color: #ccc;
}
:-moz-placeholder { /* Firefox 18- */
  color: #ccc;
}
.buttonSec {
  display: flex;
  justify-content: flex-end;
}
.backBtn {
  background: transparent;
  font-size: 16px;
  padding: 8px 25px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 20px;
  color: #666;
}
.continueBtn{
  background: #0077FF;
  font-size: 16px;
  padding: 8px 25px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 20px;
  color: #fff;
}
button{
  cursor: pointer;
}
div[data-rel*="active"]{
  display: flex;
}
@media only screen and (max-width:767px){
  .formContainer{
    width: 100%;
    margin: auto;
    box-shadow: none;
    padding: 22px;
    background-color: #fff;
    box-sizing: border-box;
  }
  .inputField{
    width: 100%;
  }
}
  
