.formCont{
  padding:30px 22px;
  overflow-x: auto;
}
.mainTab{
  width: 1024px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.mainTab li p:first-child{
  width: 40px;
  background-color: #EEF1F7;
  padding: 9px 0;
  border-radius: 100%;
  color: #ccc;
  font-weight: 600;
  text-align: center;
  margin: auto;
}
.activeTab p:first-child{
  background-color: #0077FF !important;
  color: #fff !important;
}
.activeTab p:nth-child(2){
  color: #0077FF !important;
  font-weight: bold;
}
.mainTab::after{
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  width: 85%;
  height: 1px;
  border-bottom: 5px dotted #EEF1F7;
  top: 17px;
  z-index: -1;
}
.formContainer{
  width: 80%;
  margin: auto;
  box-shadow: 0 0 10px 0 rgba(0,0,0,.15);
  padding:50px 100px;
  background-color: #fff;
  box-sizing: border-box;
}
.formSec{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.formSec{
  display: none;
}
h2{
  width: 100%;
}
.inputField{
  width: 30%;
  margin-bottom: 30px;
}
.inputField:nth-child(5),.inputField:nth-child(6){
  width: 100%;
}
.info{
  width: 16px;
  vertical-align: middle;
}
label{
  display: block;
  margin-bottom: 10px;
}
input,select,textarea{
  width: 100%;
  height: 40px !important;
  display: block;
  padding: 0;
  border: none;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding-left: 5px;
  color: #666;
}
select{
  height: 42px !important;
  color: #ccc;
}
textarea,#fileAttachment{
  height: 80px !important;
}
::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #ccc;
}
::-moz-placeholder { /* Firefox 19+ */
  color: #ccc;
}
:-ms-input-placeholder { /* IE 10+ */
  color: #ccc;
}
:-moz-placeholder { /* Firefox 18- */
  color: #ccc;
}
.buttonSec {
  display: flex;
  justify-content: flex-end;
}
.backBtn {
  background: transparent;
  font-size: 16px;
  padding: 8px 25px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 20px;
  color: #666;
}
.continueBtn{
  background: #0077FF;
  font-size: 16px;
  padding: 8px 25px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 20px;
  color: #fff;
}
button{
  cursor: pointer;
}
div[data-rel*="active"]{
  display: flex;
}
@media only screen and (max-width:767px){
  .formContainer{
    width: 100%;
    margin: auto;
    box-shadow: none;
    padding: 22px;
    background-color: #fff;
    box-sizing: border-box;
  }
  .inputField{
    width: 100%;
  }
}
  