header{
    padding:10px 5%;
    background-color: #fff;
    box-shadow: 0 0 10px 0 rgba(0,0,0,.3);
}
nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.logo{
    width: 200px;
}
.userProfile{
    display: flex;
    align-items: center;
}
.userProfile p {
    margin-right: 10px;
    width: 46px;
    background-color: #0077FF;
    padding: 11px 0;
    border-radius: 100%;
    color: #fff;
    font-weight: 600;
    text-align: center;
}
.profile{
    position: relative;
    cursor: pointer;
    color: #666;
    font-weight: 500;
}
.dropdownIcon{
    width: 16px;
    margin-left: 10px;
}
ul.profileDropdown{
    position: absolute;
    left: 0;
    width: 100%;
    text-align: center;
    background-color: #f2f2f2;
    top: 100%;
    display: none;
}
ul.profileDropdown li{
    padding: 10px 5px;
    border-bottom: 1px solid #ccc;
    cursor: pointer;
}
.profile:hover .profileDropdown{
    display: block;
}
.mobileShow{
    display: none;
}
@media only screen and (max-width:767px){
    nav{
        display: none;
    }
    .mobileShow{
        display: block;
    }
}